body {
  background: linear-gradient(135deg, rgba(5, 12, 222, 0.507), rgba(0, 0, 0, 0.7), rgba(43, 31, 31, 0.1), rgba(5, 92, 44, 0.7), rgb(221, 4, 4));
}
* {
  box-sizing: border-box;
}
.carousel-item {
  width: 100%;
  padding: 150px 50px 265px;
  img{
    border-radius: 20px;
    width: 80%;
  }
}
p{
  font-size: 30px;
  font-family: "Permanent Marker", cursive;
  font-weight: 400;
  font-style: normal;
}
#hi:hover {
  cursor: ew-resize;
  font-size: 27px;
  font-weight: 800;
  transition: 1s;
  color: #04431c;
}
.col-6 {
  padding-top: 200px;
}
strong{
  font-family: "Spicy Rice", serif;
  font-weight: 400;
  font-style: normal;
  font-size: 40px;
  padding: 0 20px;
}
#dar {
  animation: pulse 4.5s ease-in-out infinite;
}

@keyframes pulse {
  0%, 100% {
    transform: scale(1);
    font-size: 30px;
  
  }
  50% {
    transform: scale(1.05);
    color: #3b5bdb;
    font-size: 45px;
    box-shadow: 2px 2px 4px 4px black;
    border-radius: 20px;
   
  }
}

#a:hover {
  text-transform: capitalize;
  font-size: 27px;
  font-weight: 900;
  transition: 1s;
  width: 50px;
  cursor: ns-resize;
  color: darkslateblue;
}
#web{
  transition: 2s ease-in-out;
}
#web:hover {
  color: whitesmoke;
  background-color: black;
  cursor: pointer;
  border-radius: 35px;
}
#iam {
  transition: 1s ease-in-out;
}
#iam:hover {
  font-size: 27px;
  font-weight: 1000;
  cursor: ew-resize;
  color: olive;
  text-decoration: underline;
  margin-left: -100px;
  padding: 20px 50px;
  border-radius: 10px;
}

#ht {
  transition: 2s ease-in-out;
  cursor: pointer;
}

#ht:hover {
  font-size: 100px;
  color: blueviolet;
}

#css {
  transition: 2s ease-in-out;
  cursor: pointer;
}

#css:hover {
  font-size: 100px;
  color: blueviolet;
}

#react,
#api,
#lar {
  cursor: pointer;
}

.leftDisplay {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  gap: 10px;
}
.card-img {
  width: 100%;
  height: auto; /* Let height adjust automatically */
  aspect-ratio: 1 / 1; /* Maintain a 1:1 aspect ratio */
  object-fit: cover; /* Ensure the image covers the entire area without distortion */
}
#delModal {
  .modal-body {
    text-align: center;
  }
  .modal-dialog {
    width: 250px;
  }
}
.rightDisplay {
  padding: 5px;
  margin-left: auto;
}


#reactjsmessage {
  display: none;
  position: fixed;
  top: 10%;
  left: 20%;
  transform: translateX(-50%);
  z-index: 1050;
  overflow: hidden;
  outline: 0;
  width: 500px; /* Default width for larger screens */
  margin: 0 auto;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

#reactjsmessage .modal-dialog {
  max-width: 100%;
  margin: 0 auto;
}

#reactjsmessage .modal-content {
  border-radius: 8px;
  padding: 15px;
}

@media (max-width: 768px) { 
  .row {
    display: block;
  }

  .col-5 {
    width: 100%;
  }
  .carousel-item {
    width: 100%;
    padding: 50px 50px;
    img {
      width: 100%;
    }
  }

  .col-6 {
    width: 100%;
    padding: 0;
  }

  .modal {
    
    #view {
      display: block !important;
    }
  
    .modal-dialog-lg {
      width: 100% !important;
      margin: 0 !important;
      padding: 0 10px;
    }
  
    .modal-content {
      padding: 10px;
    }
  
    .modal-header h5 {
      font-size: 18px;
    }
  
    .modal-body .col-md-6 {
      flex: 0 0 100%;
      max-width: 100%;
    }
  
    .modal-body img {
      width: 100% !important;
    }
  
    .card-body h5 {
      font-size: 16px;
    }
  
    .card-body h6 {
      font-size: 14px;
    }
  
    .desc p {
      font-size: 14px;
    }
  
    .d-flex img {
      width: 20% !important;
      margin: 10px auto;
    }
  }

  #larvel {
    display: block !important;
    .rightDisplay {
      width: 100%;
    }
    .leftDisplay {
     .card-img {
      width: 100% !important;
      object-fit: cover;
      padding: 0 !important;
      
     }
    }
   
  }
  #rightContent {
    padding: 40px;
  }
  #dar {
    animation: pulse 4.5s ease-in-out infinite;
  }
  
  @keyframes pulse {
    0%, 100% {
      transform: scale(1);
      font-size: 20px;
    
    }
    50% {
      transform: scale(1.05);
      color: #3b5bdb;
      font-size: 30px;
      box-shadow: 2px 2px 4px 4px black;
      border-radius: 20px;
     
    }
  }
 
  #reactjsmessage {
    width: 90%; /* Take up most of the screen width */
    top: 5%; /* Adjust top spacing */
    left: 30%;
  }
}